import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			collectList:[],
			loaded:false,
			loadingType:'loading',
			page:1,
			isFristShow:true,
			goods_id:'-1'
		}
	},
	onLoad() {
		this.loadData();
		this.isFristShow=false;
	},
	onShow() {
		if(!this.isFristShow){
			//this.page=1;
			//this.loadData();
			//页面的刷新效果处理  详情页面返回的时候 本地动态地刷新界面
			let self=this;
			uni.getStorage({
				key:'needRemoveGoodsId',
				success(res) {
				self.goods_id=res.data;
				if(self.goods_id!='-1'){
					for (let index in self.collectList) {
						if(self.collectList[index].goods_id==self.goods_id){
							self.collectList.splice(index,1);
							self.goods_id='-1';
							uni.setStorage({
								key:'needRemoveGoodsId',
								data:'-1'
							})
							break;
						}
					}
				}	
				}
			})
		}
	},
	onPullDownRefresh() {
		let self=this;
		setTimeout(function(){
			self.page=1;
			self.loadData();
			uni.stopPullDownRefresh();
		},800);
	},
	methods: {
		//异步请求  收藏列表的数据
		async loadData() {
			let self = this;
			self.$axios(self.$baseUrl.getcollectionList,
			{page:self.page})
			.then(res =>{
				if(res.data.code == 1){
					let serverList=res.data.data.data;
					if(self.page==1){
						self.collectList=[];
					}
					for (let index in serverList) {
						self.collectList.push(serverList[index]);
					}
					self.loaded=true;
					if(serverList.length<10){
						self.$data.loadingType='nomore'
					}else{
						self.$data.loadingType='loading'
					}
					
				}else{
					self.$api.msg(res.data.msg);
				}
				
			},(error) =>{
				console.log(error)
			})
		
		},
		loadMore(){
			//如果没有更多了  不需要调接口了
			if(this.loadingType==='nomore')return;
			let self=this;
			setTimeout(function(){
				self.page+=1;
				self.loadData();
			},800);
			
		},
		//详情页
		navToDetailPage(item) {
			let id = item.goods_id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
	}
}
